.techin-t{
    &-wrap{
        h2{
            padding-bottom: 30px;
        }
        @include max (1199px){
            padding-bottom: 50px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-slider-wraper {
        display: flex!important;
        gap: 30px;
        @include max (767px){
            display: block!important;
        }
    }
    &-slider-wrap{
        padding: 60px 40px;
        position: relative;
        background-position: center;
        background-size: 100% 100%;
        @include max (767px){
            &.mb-30{
                margin-bottom: 30px;
            }
        }
    }
    &-slider-title{
        padding-bottom: 24px;
        h6{
            margin-bottom: 5px;
        }
        p{
            font-size: 14px;
            line-height: 1.71em;
        }
    }
    &-slider-thumb{
        position: absolute;
        top: 0;
        right: 40px;
        @include max (991px){
            position: static;
            margin-bottom: 30px;
        }
    }
    &-slider-rating{
        padding-bottom: 18px;
    }
    &-slider-icon {
        position: absolute;
        bottom: 0;
        right: 40px;
    }
    // end one
    &-wrap2{
        padding: 30px 25px;
        border-radius: 10px;
        position: relative;
        margin-top: 30px;
        transition: all 0.4s;
        background-color: var(--light-color);
        &:hover{
            background-color: var(--accent-bg);
            .techin-t-text{
                p{
                    color: var(--white-color);
                }
            }
            .techin-t-author-content{
                h6{
                    color: var(--white-color);
                }
                p{
                    color: var(--white-color);
                }
            }
            .techin-t-ratting{
                path{
                    fill: var(--white-color);
                }
            }
            .shape-hide{
                opacity: 1;
            }
            .techin-t-author-icon{
                path{
                    fill: var(--white-color);
                }
            }
        }
    }
    &-thumb{
        position: absolute;
        top: -30px;
        right: 50px;
        border: 10px solid #fff;
        border-radius: 20px;
        z-index: 2;
        img{
            border-radius: 10px;
        }
    }
    &-ratting{
        transition: all 0.4s;
        padding-bottom: 20px;
    }
    &-text{
        p{
            padding-bottom: 20px;
            font-size: 14px;
            line-height: 1.71em;
            transition: all 0.4s;
        }
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-author-content{
        transition: all 0.4s;
        h6{
            padding-bottom: 5px;
        }
        p{
            color: var(--accent-color2);
            font-size: 14px;
            line-height: 1.71em;
        }
    }
}



.techin-t-slider-init .techin-t-slider-init-next, 
.techin-t-slider-init .techin-t-slider-init-prev {
    bottom: 20px;
    left: -51%;
    width: 60px;
    height: 60px;
    z-index: 1;
    border: 1px solid var(--accent-color);
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 100px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--white-color);
    transform: rotate(0deg);
    background-image: url(../images/v1/arrow.svg);
    @include max (1199px){
        top: -130px;
    }
    @include max (991px){
        display: none!important;
    }
}
.techin-t-slider-init .techin-t-slider-init-prev {
    left: -42%;
    transform: rotate(180deg);
    right: auto;
    background-image: url(../images/v1/arrow.svg);
    @include max (991px){
        display: none!important;
    }
}

