.techin-about{
    &-thumb{
        @include max (991px){
            padding-bottom: 40px;
            text-align: center;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            
        }
        &.thumb2{
            text-align: end;
            padding-right: 50px;
            @include max (991px){
                padding-right: 0;
                text-align: center;
            }
        }
    }
    &-thumb2{
        position: absolute;
        bottom: -130px;
        right: -15px;
        @include max (1399px){
            display: none;
        }
        &.thumb2{
            bottom: 0;
            right: 310px;
            left: 0;
            top: 210px;
        }
    }
    &-tag{
        position: absolute;
        top: 50px;
        right: 110px;
        display: flex;
        gap: 10px;
        img{
            height: 100%;
            border-radius: 0;
        }
        h6{
            color: var(--accent-color);
        }
        @include max (1399px){
            display: none;
        }
    }
    &-frame{
        position: absolute;
        bottom: -140px;
        left: -140px;
        z-index: -1;
        
        @include max (1550px){
            display: none;
        }
        &.frame1{
            bottom: -80px;
            left: 0;
            right: 150px;
        img{
            height: 300px;
        }
        }
    }
    &-content{
        h2{
            padding-bottom: 40px;
        }
        span.title{
            color: var(--accent-color);
        }
    }
    &-info-wraper {
        display: flex;
        gap: 30px;
        @include max (575px){
            display: block;
        }
    }
    &-info-wrap {
        display: flex;
        gap: 20px;
        align-items: center;
        @include max (575px){
            padding-top: 30px;
        }
        ul{
            li{
                display: inline-block;
                position: relative;
                img{
                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }
        &.wrap2{
            position: absolute;
            top: 50px;
            left: 0;
            text-align: left;
            border-radius: 20px;
            padding: 20px;
            background-color: var(--accent-bg);
            img{
                border-radius: 6px;
                height: auto;
            }
            @include max (1399px){
                display: none;
            }
        }
    }
    &-info-text{
        a{
            color: var(--body-color);
            display: block;
            span{
                font-family: $heading-font;
                font-size: 16px;
                font-weight: 700;
                line-height: 26px;
                color: var(--heading-color);
            }
        }
        &.text2{
            a{
                color: var(--white-color);
                span{
                    font-family: $heading-font;
                    color: var(--white-color);
                }
            }
        }
    }
    
}
.techin-title-tag{
    display: flex;
    gap: 10px;
    padding-bottom: 10px;
    h6{
        color: var(--accent-color);
    }
    &.center2{
        justify-content: center!important;
    }
    &.pricing-v1{
        h6{
            
            color: var(--white-color);
        }
    }
    &.tag2{
        h6{
            color: var(--white-color);
        }
    }
}
.pl-60{
    padding-left: 30px;
    @include max (991px){
        padding-left: 0;
    }
}