.techin-popup{
    &-video {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        img{
            @include max (991px){
                width: 84%;
            }
        }
        
    }
    &-video2 {
        position: absolute;
        top: -70px;
        left: 45%;
        padding: 15px;
        border-radius: 100px;
        animation: animName 10s linear infinite;
        background-color: var(--accent-bg);
        @include max (991px){
            top: 25%;
            left: 41%;
            transform: translate(-50%, -50%);
        }
        @include max (767px){
            top: 18%;
            left: 38%;
        }
        @include max (575px){
            top: 3%;
            left: 32%;
        }
    }
    &-video3 {
        display: inline-block;
        padding-bottom: 50px;
        position: relative;
        z-index: 1;
        @include max (991px){
            padding-bottom: 40px;
        }
        img{
            @include max (991px){
                width: 84%;
            }
        }
    }
    
}
.play-btn {
    position: absolute;
    top: -36px;
    left: 606px;
    @include max (1399px){
        left: 536px;
    }
    @include max (1199px){
        left: 457px;
    }
    @include max (991px){
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
@keyframes animName {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background: var(--accent-bg2);
    opacity: 0;
    border-radius: 100%;
    left: -47px;
    top: -47px;
    z-index: 0;
    transform: translate(-50px, -50px);
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    &.waves2{
        left: -45px;
        top: -40px;
        background-color: var(--accent-bg);
        @include max (991px){
            display: none;
        }
    }
    &.waves3{
        right: 0;
        top: -44px;
        z-index: -1;
        background-color: var(--white-bg);
        @include max (991px){
            display: none;
        }
    }
    @include max(991px) {
      width: 160px;
      height: 160px;
      left: -30px;
      top: -30px;
    }
    &.wave-1 {
      -webkit-animation-delay: 0s;
      animation-delay: 0s;
    }
    &.wave-2 {
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
    &.wave-3 {
      -webkit-animation-delay: 2s;
      animation-delay: 2s;
    }
  }
  
  
  @keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }
  
    50% {
        opacity: 0.9;
    }
  
    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
}