.techin-team{
    &-wrap{
        transition: all 0.4s;
        position: relative;
        margin-bottom: 48px;
        &:hover {
            .techin-team-thumb{
                &::before {
                    background: var(--accent-bg)!important;
                }
            }
            .techin-team-social {
                opacity: 1;
            }

        }
    }
    &-thumb{
        z-index: 1;
        position: relative;
        border-radius: 20px;
        img{
            width: 275px;
            border-radius: 20px;
            object-fit: cover;
            transition: all .4s;
            @include max (1399px){
                width: 240px;
            }
            @include max (1199px){
                width: 94%;
            }
            @include max (767px){
                width: 100%;
            }
        }
        &::before{
            content: "";
            left: -30px;
            top: -30px;
            width: 97%;
            height: 101.5%;
            z-index: -1;
            transition: all .4s;
            position: absolute;
            border-radius: 20px;
            background-color: var(--white-bg);
            border: 2px solid var(--light-color2);
            @include max (767px){
                display: none;
            }
        }
    }
    &-content{
        padding: 20px 0 20px 20px;
        width: 85%;
        background: var(--accent-bg2);
        border-radius: 0 10px 10px 0;
        position: absolute;
        bottom: 30px;
        z-index: 1;
        P{
            color: var(--heading-color);
            font-size: 14px;
            line-height: 1.7em;
            margin-bottom: 0;
        }
    }
    &-share-icon {
        position: absolute;
        top: -20px;
        right: 20px;
    }
    // end one
    &-wrap2{
        text-align: center;
        padding: 40px 30px;
        border-radius: 20px;
        position: relative;
        transition: all 0.4s;
        margin-bottom: 24px;
        border: 1px solid var(--light-color2);
        background-color: var(--light-color);
        &:hover {
            border: 1px solid var(--accent-bg);
            .techin-team-social {
                opacity: 1;
                ul{
                    li{
                        margin-bottom: 10px;
                        padding: 7px 12px;
                        border-radius: 6px;
                        transition: all 0.4s;
                        background-color: var(--accent-bg);
                        &:last-child {
                            margin-bottom: 0;
                        }
                        &:hover{
                            background-color: var(--accent-bg2);
                        }
                    }
                }
            }
            .techin-team-content2{
                h5{
                    color: var(--accent-color);
                }
                p{
                    color: var(--accent-color2);
                }
            }
        }
    }
    &-thumb2{
        padding: 0 35px 20px;
    }
    &-content2{
        padding-bottom: 20px;
        border-bottom: 1px solid var(--light-color2);
        margin-bottom: 20px;
        h5{
            transition: all 0.4s;
            padding-bottom: 10px;
        }
        p{
            width: 200px;
            font-size: 14px;
            font-weight: 400;
            line-height: 24px;
            padding: 3px 20px;
            border-radius: 20px;
            margin: 0 auto;
            transition: all 0.4s;
            color: var(--heading-color);
            background-color: var(--light-color2);
        }
    }
    &-info-wraper{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-info-wrap{
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--accent-bg);
        gap: 10px;
        border-radius: 6px;
        padding: 7px 13px;
        transition: all 0.4s;
        &:hover{
            background-color: var(--accent-bg2);
        }
    }
    &-info-content{
        h5{
            font-size: 16px;
            line-height: 26px;
            color: var(--white-color);
        }
    }
}