.techin-faq{
    &-content-wrap{
        padding: 110px 0 106px;
        margin-left: 50px;
        @include max (991px){
            margin-left: 0;
            padding: 60px 0 56px;
        }
        @include max (767px){
            padding: 40px 0 36px;
        }
        @include max (575px){
            padding: 30px 0;
        }
        .faq-title{
            padding-bottom: 50px;
            @include max (991px){
                padding-bottom: 40px;
            }
        }
    }
    &-thumb{
        margin-left: calc((-100vw + 100%) / 12);
        position: relative;
        @include max (1850px){
            margin: 0;
            padding: 110px 0 106px;
        }
        @include max (991px){
            padding: 80px 0 0;
        }
        @include max (767px){
            padding: 40px 0 0;
        }
        img{
            width: 100%;
            border-radius: 0 10px 10px 0;
            @include max (1850px){
                border-radius: 10px;
            }
        }
        &::before{
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            border-radius: 0 10px 10px 0;
            background: rgba(34, 38, 39, 0.8);
            @include max (1850px){
                top: 110px;
                left: 0;
                width: 100%;
                height: 74%;
                border-radius: 10px;
            }
            @include max (1399px){
                height: 71.5%;
            }
            @include max (1199px){
                height: 67.5%;
            }
            @include max (991px){
                top: 80px;
                height: 89.5%;
            }
            @include max (767px){
                top: 40px;
                height: 93%;
            }
            @include max (575px){
                top: 40px;
                height: 90%;
            }
        }
    }
    &-item{
      position: relative;
      margin-bottom: 20px;
      border-radius: 10px;
      transition: all 0.4s;
      &.open {
        .techin-faq-active-icon{
            background: var(--white-bg);
            svg{
                transform: rotate(180deg);
            }
            path{
                fill: var(--accent-bg);
            }
          }
          .techin-faq-body{
            display: block;
            padding: 54px 30px 30px;
            background: var(--white-bg);
            border: 3px solid var(--accent-bg2);
            margin-top: -30px;
            border-radius: 0 0 6px 6px;
            transition: all 0.4s;
            p{
                font-size: 14px;
                transition: all 0.4s;
                line-height: 1.71em;
                color: var(--light-color5);
            }
          }
          .techin-faq-header{
            background: var(--accent-bg);
            h5,h6{
                color: var(--white-color);
            }
          }
      }
    }
    &-header{
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: var(--white-bg);
        padding: 5px 0 5px 30px;
        border-radius: 6px;
        transition: all 0.4s;
      h6,h5{
        @include max(767px) {
          font-size: 24px;
          padding-right: 25px;
        }
      }
     
    }
    &-body{
      display: none;
      padding-top: 15px;
      &.default{
        display: block;
      }
      @include max(991px) {
        padding: 24px 0 0;
      }
    }
    &-active-icon{
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        transition: all 0.4s;
        margin-right: 5px;
        background: var(--accent-bg2);
        svg{
            transition: all 0.4s;
        }
    }
    // end one
    &-wrap {
        max-width: 900px;
        margin: 0 auto;
    }
}


