.techin-service{
    &-wrap{
        text-align: center;
        padding: 36px 27px;
        transition: all 0.4s;
        position: relative;
        margin-bottom: 24px;
        z-index: 0;
        &:hover{
            .techin-service-icon{
                background-color: var(--white-bg);
                path{
                    fill: var(--accent-color);
                }
            }
            .techin-service-content{
                h5,p{
                    color: var(--white-color);
                }
            }
            .techin-service-btn{
                background: var(--accent-bg2);
            }
            
        }
        &:hover::before, &:hover::after{
            background-color: var(--accent-bg);
        }
        &::before, &::after{
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
            transition: all 0.4s;
            border-radius: 0 100px 0 0;
            background-color: var(--accent-bg2);
        }
        &::after{
            border-radius: 0 150px 0 0;
            background-color: var(--white-bg);
        }
    }
    &-icon{
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        transition: all 0.4s;
        background: var(--accent-bg);
        margin: 0 auto 20px;
    }
    &-content{
        padding-bottom: 20px;
        h5{
            padding-bottom: 10px;
        }
        p{
            font-size: 14px;
            line-height: 1.7em;
        }
    }
    // end one
    &-wraper2{
        margin-top: -315px;
    }
    &-wrap2{
        position: relative;
        transition: all 0.4s;
        border-radius: 20px;
        padding: 20px 20px 40px 20px;
        border: 1px solid var(--light-color2);
        background-color: var(--light-color);
        &:hover{
            border: 1px solid var(--accent-color);
            .techin-service-btn{
                background-color: var(--accent-bg2);
            }
            .techin-service-content2{
                h5{
                    color: var(--accent-color);
                }
            }
        }
        @include max (991px){
            margin-bottom: 50px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
    }
    &-thumb{
        position: relative;
        img{
            border-radius: 20px;
        }
    }
    &-icon2{
        padding: 0;
        z-index: 2;
        margin: 0 auto 20px;
        border-radius: 100px;
        position: absolute;
        transition: all .4s;
        display: flex;
        border: 20px solid var(--light-color);
        align-items: center;
        justify-content: center;
        flex-direction: column;
        bottom: -65px;
        left: 34%;
    }
    &-content2{
        padding: 40px 20px 0 20px;
        text-align: center;
        h5{
            padding-bottom: 10px;
        }
        p{
            margin-bottom: 24px;
        }
    }
}

.techin-three-column2 .techin-s-slider-next, 
.techin-three-column2 .techin-s-slider-prev {
    top: -130px;
    right: 80px;
    width: 60px;
    height: 60px;
    z-index: 1;
    border: 1px solid var(--white-color);
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 100px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--white-bg);
    transform: rotate(0deg);
    background-image: url(../images/v1/arrow.svg);
    @include max (1199px){
        top: -130px;
    }
    @include max (991px){
        top: -80px;
    }
    @include max (767px){
       display: none!important;
    }
}
.techin-three-column2 .techin-s-slider-prev {
    left: auto;
    transform: rotate(180deg);
    right: 0;
    background-image: url(../images/v1/arrow.svg);
    @include max (991px){
    }
}
.techin-three-column2{
    .slick-slide{
        margin: 0 12px;
    }
   
}
