.techin-p{
    &-slider-wrap {
        display: flex!important;
        gap: 30px;
        @include max (991px){
            display: block!important;
        }
    }
    &-item{
        position: relative;
        transition: all 0.4s;
        @include max (991px){
            margin-bottom: 50px;
        }
        @include max (767px){
            margin-bottom: 30px;
        }
        &:hover{
            .techin-p-content{
                background-color: var(--accent-bg);
                p,h6{
                    color: var(--white-color);
                }
            }
            .techin-p-thumb img{
                transform: scale(1.1);
            }
        }
      
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        height: 100%;
        width: 100%;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .4s;
        }
    }
    &-content {
        background-color: var(--white-bg);
        padding: 20px;
        border-radius: 20px 20px 0 0;
        position: absolute;
        bottom: 0;
        transition: all 0.4s;
        left: 40px;
        width: calc(100% - 80px);
        @include max (767px){
            padding: 20px;
        }
        p{
            color: var(--accent-color2);
            margin-bottom: 5px;
            font-size: 14px;
            line-height: 1.71em;
        }
        .btn-icon {
            position: absolute!important;
            top: -20px;
            right: 30px;
        }
    }
    // end one
    &-wrap{
        position: relative;
        transition: all 0.4s;
        margin-bottom: 24px;
        &:hover{
            .techin-p-content2{
                opacity: 1;
                bottom: 30px;
            }
            .techin-p-thumb2 img{
                transform: scale(1.1);
            }
            // .techin-p-btn2{
            //     opacity: 1;
            //     bottom: 11%;
            //     &.btn2{
            //         bottom: 22%;
            //     }
            // }
        }
    }
    &-thumb2{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        transition: all 0.4s;
        &:hover::before{
            opacity: 1;
        }
        &::before{
            content: "";
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            opacity: 0;
            border-radius: 20px;
            position: absolute;
            transition: all 0.4s;
            background-color: rgba(34, 38, 39, 0.8);
            @include max (767px){
                display: none;
            }
        }
        img{
            width: 100%;
            border-radius: 20px;
            object-fit: cover;
            transition: all .4s;
        }
    }
    &-content2{
        position: absolute;
        bottom: 0;
        transition: all 0.4s;
        left: 30px;
        z-index: 2;
        background-color: var(--white-bg);
        border-radius: 10px;
        padding: 20px 59px 20px 30px;
        max-width: 400px;
        opacity: 1;
        p{
            margin-bottom: 5px;
            color: var(--accent-bg2);
            font-size: 14px;
            line-height: 1.71em;
        }
        @include max (767px){
            position: inherit;
            opacity: 1;
            visibility: visible;
            width: 100%;
            left: 0;
            border-radius: 0;
        }
    }
    &-btn2{
        position: absolute;
        right: -24px;
        top: 44px;
        border: 30px solid hsla(0, 0%, 0%, .2);
        img{
            width: 40px;
            height: 40px;
        }
    }
    &-btn{
        padding-top: 16px;
        text-align: center;
    }
}


.techin-three-column .techin-p-slider-next, 
.techin-three-column .techin-p-slider-prev {
    top: -130px;
    right: 80px;
    width: 60px;
    height: 60px;
    z-index: 1;
    border: 1px solid var(--accent-color);
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 100px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--white-color);
    transform: rotate(0deg);
    background-image: url(../images/v1/arrow.svg);
    @include max (1199px){
        top: -130px;
    }
    @include max (991px){
        top: -80px;
    }
    @include max (767px){
       display: none!important;
    }
}
.techin-three-column .techin-p-slider-prev {
    left: auto;
    transform: rotate(180deg);
    right: 0;
    background-image: url(../images/v1/arrow.svg);
    @include max (991px){
    }
}

.techin-three-column{
    .slick-slide{
        margin: 0 12px;
    }
   
}
