.techin-iconbox{
    &-wraper {
        display: flex;
        padding-top: 30px;
        padding-bottom: 50px;
        justify-content: space-between;
        ul{
            li{
                display: inline-flex;
                gap: 20px;
            }
        }
        @include max (767px){
            display: block;
            padding-bottom: 0;
        }
    }
    &-wrap{
        ul{
            li{
                display: inline-block;
                position: relative;
                padding-left: 95px;
                font-family: $heading-font;
                font-size: 24px;
                font-weight: 700;
                line-height: 34px;
                color: var(--heading-color);
                @include max (767px){
                    margin-bottom: 60px;
                }
                @include max (575px){
                    margin-bottom: 40px;
                }
                img{
                    position: absolute;
                    bottom: 0px;
                    left: 0;
                    @include max (767px){
                        bottom: -24px;
                    }
                    @include max (575px){
                        bottom: 0;
                    }
                }
            }
        }
    }
    &-wrap2-item1{
        padding: 32px 30px;
        border-radius: 20px;
        position: relative;
        margin-bottom: 24px;
        background: var(--light-color);
    }
    &-wrap2-item3{
        padding: 32px 30px;
        border-radius: 20px;
        position: relative;
        margin-bottom: 24px;
        background: var(--light-color);
    }
    &-wrap2-item2{
        padding: 32px 30px;
        border-radius: 20px;
        position: relative;
        margin-bottom: 24px;
        background: var(--light-color);
    }
    &-wrap2-item4{
        padding: 32px 30px;
        border-radius: 20px;
        position: relative;
        margin-bottom: 24px;
        background: var(--light-color);
    }
    &-title-wrap2 {
        display: flex;
        align-items: center;
        gap: 20px;
        padding-bottom: 24px;
    }
    &-title-thumb{
        padding: 40px;
        @include max (1450px){
            padding: 30px;
        }
        @include max (1199px){
            padding: 30px;
            margin-top: 100px;
        }
        @include max (991px){
            margin: 40px 0;
            padding: 0;
        }
        @include max (767px){
            margin: 0 0 15px 0;
            padding: 30px;
        }
    }
    &-number-icon {
        position: absolute;
        top: -6px;
        right: -3px;
        border: 6px solid var(--white-color);
        border-radius: 100px;
        @include max (991px){
            display: none;
        }
        &.icon2{
            border: 6px solid var(--light-color);
        }
    }
}
.item{
    background-color: var(--white-bg);
}
.mb-0{
    @include max (767px){
        margin-bottom: 0;
    }
}