.techin-section-padding{
    padding: 120px 0 190px;
    @include max (1199px){
        padding: 120px 0 100px;
    }
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}

.techin-section-padding2{
    padding: 110px 0 106px;
    @include max (991px){
        padding: 80px 0 76px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.techin-section-padding3{
    padding: 120px 0 236px;
    @include max (991px){
        padding: 80px 0 76px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.techin-section-padding4{
    padding: 110px 0 315px;
    @include max (991px){
        padding: 80px 0 315px;
    }
    @include max (767px){
        padding: 60px 0 315px;
    }
    @include max (767px){
        padding: 60px 0 315px;
    }
}
.techin-section-padding5{
    padding: 120px 0 106px;
    @include max(991px) {
      padding: 100px 0 76px;
    }
    @include max(767px) {
      padding: 80px 0 56px;
    }
}
.techin-section-padding6{
    padding: 120px 0;
    @include max(991px) {
      padding: 100px 0;
    }
    @include max(767px) {
      padding: 60px 0;
    }
}
.techin-section-padding7{
    padding: 0 0 106px;
    @include max (991px){
        padding: 0 0 76px;
    }
    @include max (767px){
        padding: 0 0 36px;
    }
}
.techin-section-padding8{
    padding: 120px 0 240px;
    @include max (1199px){
        padding: 120px 0 100px;
    }
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.techin-section-title{
    padding-bottom: 50px;
    @include max (991px){
        padding-bottom: 40px;
    }
    @include max (767px){
        padding-bottom: 30px;
    }
    h6{
        color: var(--accent-color);
    }
    &.techin-pricing{
        h2{
            color: var(--white-color);
        }
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
    }
    &.version2{
        h2{
            color: var(--white-color);
        }
    }
    
}
.max-width-645{
    max-width: 645px;
}
.mb-50{
    margin-bottom: 80px!important;
    padding: 0;
    @include max (991px){
        margin-bottom: 50px!important;
    }
    @include max (767px){
        margin-bottom: 30px!important;
    }
}
.mb-65{
    margin-bottom: 65px!important;
    @include max (1570px){
        margin-bottom: 50px!important;
    }
    @include max (1199px){
        margin-bottom: 40px!important;
    }
    @include max (991px){
        margin-bottom: 50px!important;
    }
    @include max (767px){
        margin-bottom: 30px!important;
    }
}
.section{
    background-position: center;
    background-size: cover;
}